import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';




const osName = platform();

const Prog11 = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="program">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			
		</PanelHeader>
		<div className="container_p">

		<div className="cont1_p">
		<h1 className="yprag_p" align="center">Взрывные подтягивания: 4 х 10</h1>
		<div className="chill_p"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>


		<div className="cont2_p">
		<h1 className="yprag_p" align="center">Взрывные отжимания: 4 х 15</h1>
		<div className="chill_p"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>

		<div className="cont3_p">
		<h1 className="yprag_p" align="center">Скручивания: 5 х 20</h1>
		<div className="chill_p"><h1 className="chl">Отдых:2 минуты</h1></div>
		</div>

		<div className="cont4_p">
		<h1 className="yprag_p" align="center">Стульчик: 4 х 1 мин</h1>
		<div className="chill_p"><h1 className="chl">Отдых:2 минуты</h1></div>
		</div>

		<div className="cont5_p">
		<h1 className="yprag_p" align="center">Лодочка: 5 х 1 мин</h1>
		<div className="chill_p"><h1 className="chl">Отдых:2 минуты</h1></div>
		</div>

		<div className="cont6_p">
		<h1 className="yprag_p" align="center">Взрывные приседания: 5 х 13</h1>
		<div className="chill_p"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>

		<div className="cont7_p" align="center">
		<h1 className="yprag_p">Берпи: 4 х 30</h1>
		<div className="chill_p"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>



		</div>	


		</Panel>
);

Prog11.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Prog11;
